.text-uppercase{
  text-transform: uppercase!important;
}
.fw-800{
  font-weight: 800!important; 
}
.home-container {
  width: 100%;
  height: 100%;
  min-height: 90vh;
  margin-top: 10vh;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  text-align: center;
  justify-content: center;
}



.home-container .content {
  width: 100%;
  padding: 0 1rem;
  margin-bottom: 2rem;
}


.content h2 {
  font-size: 2rem;
  font-weight: bold;
}
.content p {
  display: block;
  font-size: 1.5rem;
  margin: 1rem 0;
  padding: 0 1rem;
  color: rgb(97, 89, 89);
}

.content .home-btn {
  font-size: large;
  display: inline-block;
  margin-bottom: 1rem;
  background-color: var(--primary-color);
  color: white;
  padding: 1rem 2rem;
  border-radius: 1px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  margin-top: 1rem;
  font-weight: bold;
  border: 1px solid transparent;
}

.home-btn:hover {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background-color: transparent;
}
.text-21{
  font-size: calc(1.5rem + 1.6vw)!important;
}

/* Medium */
@media screen and (max-width: 768px) {
  .content h2{
    height: 10rem;
  }
}


/* Large */
@media screen and (min-width: 900px) {
  .home-container {
    flex-direction: row;
    align-items: center;
    text-align: left;
    justify-content: center;
  }

  .home-container .svg {
    display: block !important;
    width: 90%;
    height: 90%;
  }

  .svg img {
    width: 100%;
    height: 100%;
  }

  .home-container .content {
    padding: 0 1rem;
    text-align: left;
  }

  .content h1 {
    font-size: 4rem;
  }

  .content p {
    padding: 0;
    width: 30rem;
  }
}

